<template lang="pug">
  .create
    h1.title 
    el-form(:model="create", :rules="rules", ref="create", label-position="left")
      .create-card
        el-form-item(prop="isPublic", required="")
          el-switch(v-model="create.isPublic", active-text="", inactive-text="")
        el-form-item(label="", prop="title", required="")
          el-input(v-model.trim="create.title", placeholder="")
        el-form-item(label="", prop="type")
          el-select(v-model="create.type", placeholder="")
            el-option(label="", value=0)
            el-option(label="", value=1)
        el-form-item(label="", prop="max", v-if="+create.type")
          el-input(v-model.number="create.max", placeholder="")
            el-option(label="", value=0)
            el-option(label="", value=1)
        el-form-item(label="", prop="password", v-if="!create.isPublic")
          el-input(v-model="create.password", placeholder="")
      .create-card
        el-form-item(label="", required="")
          el-form-item(prop="startAt")
            el-date-picker(v-model="create.startAt", type="datetime", placeholder="")
        el-form-item(label="", required="")
          el-form-item(prop="endAt")
            el-date-picker(v-model="create.endAt", type="datetime", placeholder="")
      .create-card
        el-form-item(v-for="(option, index) in create.options", :label="'' + (index + 1)", :key="option.key", :prop="'options.' + index + '.value'", :rules="{required: true, message: '', trigger: 'blur'}")
          .options
            el-input.option-item(v-model="option.value")
            el-button.option-button(@click.prevent="removeOption(option)") 
        el-form-item
          el-button.el-button.add-button(@click="addOption") 
          // <el-button @click="resetForm('create')"></el-button>
      el-form-item
        .submit-button-wrapper
          el-button.el-button(@click="submitForm('create')", type="primary") 
        // <el-button @click="resetForm('create')"></el-button>
</template>

<script>
  import { mapActions, mapState } from 'vuex'

  export default {
    data () {
      const validatePass = (rule, value, callback) => {
        if (value === null && !this.create.isPublic) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        create: {
          title: '',
          password: null,
          type: '',
          max: 1,
          startAt: '',
          endAt: '',
          isPublic: 1,
          options: [{
            value: ''
          }]
        },
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 20, message: ' 3  20 ', trigger: 'blur'}
          ],
          type: [
            {required: true, message: '', trigger: 'change'}
          ],
          max: [
            {required: true, message: '', trigger: 'blur'}
          ],
          startAt: [
            {type: 'date', required: true, message: '', trigger: 'change'}
          ],
          endAt: [
            {type: 'date', required: true, message: '', trigger: 'change'}
          ],
          password: [
            {validator: validatePass, trigger: 'blur'}
          ]
        }
      }
    },
    computed: {
      ...mapState(['vote'])
    },
    methods: {
      ...mapActions(['createVoteAction']),
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const opt = this.create
            if (opt.isPublic) {
              opt.password = null
            }
            this.createVoteAction(opt).then(() => {
              console.log(this.vote)
              this.$message({
                showClose: true,
                type: 'success',
                message: 'create successful'
              })
            })
          } else {
            this.$message({
              showClose: true,
              type: 'error',
              message: 'submit error'
            })
            return false
          }
        })
      },
      removeOption (item) {
        let index = this.create.options.indexOf(item)
        if (index !== -1) {
          this.create.options.splice(index, 1)
        }
      },
      addOption () {
        this.create.options.push({
          value: '',
          key: Date.now()
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-input, .el-select {
    width 100%
    max-width: 400px
  }

  .el-button {
    width 100%
    max-width 480px
  }

  .create {
    width 30%
    min-width 500px
    margin 0 auto
    .create-card {
      background-color #ffffff
      margin 10px auto
      padding 20px 50px
      border-radius 5px
      border 1px solid #d8dee2

      .options {
        width 100%
        clear: both;
        text-align left
        .option-item {
          width 78%
        }
        .option-button {
          padding-left 0
          padding-right 0
          width 20%
        }
      }
      .create-button {
        width 100%
      }
      .add-button {
        width 100%
        border 1px dashed #d9d9d9
      }
    }
  }

  .line {
    border 1px solid #d8dee2
  }

  @media screen and (max-width: 500px) and (min-width: 400px) {
    .el-input, .el-select {
      max-width: 400px
    }

    .el-picker-panel .el-date-picker .el-popper {
      width 100%
    }

    .create {
      min-width 400px
      .create-card {
        padding 20px 20px
      }
    }
  }

  @media screen and (max-width: 400px) {
    .el-input, .el-select {
      max-width: 300px
    }

    .el-picker-panel .el-date-picker .el-popper {
      width 100%
    }

    .create {
      min-width 300px
      .create-card {
        padding 20px 20px
      }
    }
  }

</style>



