'use strict'

/**
 * 
 * @param ms 
 * @returns {Promise}
 */
export function sleep (ms) {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
    console.log(`sleep ${ms / 1000}s`)
  })
}

/**
 *  url
 * @param url
 * @param params
 * @returns {*}
 */
export function params2url (url, params) {
  if (params) {
    if (typeof params === 'object') {
      Object.keys(params).forEach(key => {
        if (params[key]) {
          url = url + '/' + params[key]
        }
      })
    } else {
      url = url + '/' + params
    }
  }
  return url
}

/**
 *  url 
 * @param url  base Url
 * @param query query
 * @returns {*}
 */
export function query2url (url, query) {
  let dataStr = '' // 
  if (query) {
    Object.keys(query).forEach(key => {
      dataStr += key + '=' + query[key] + '&'
    })
    if (dataStr !== '') {
      dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'))
      url = url + '?' + dataStr
    }
  }
  return url
}

/**
 * 
 * @param that this
 * @param path 
 * @param fix  '/'
 */
export function goto (that, path, fix = true) {
  //  fix 
  if (path[0] !== '/' && fix) {
    let pathArr = path.split('')
    pathArr.unshift('/')
    path = pathArr.join('')
  }
  that.$router.push(path)
}

/**
 * 
 * @param that  this
 * @param path 
 * @param ms 
 * @returns {Promise.<void>}
 */
export async function lazyGoto (that, path, ms = 1000) {
  await sleep(ms)
  goto(that, path)
}



